        check_button_jejusi() {
                if ( !cafeIndex ) {
                    jejucafeIndex = jejucafeIndex ? false : true
                    seogwipocafeIndex = false
                    cafeIndex = false                                
                    this.displayCafe(false)
                }
                if ( !restIndex ) {
