        get_polylineLayer(fname) {

            let realcolor='';
            let backcolor='';

            if (cataindex==1) {
                realcolor = '#0004FF';
                backcolor = '#FF0000';      
            } else if (cataindex==2) {
                realcolor = '#FF0000';
                backcolor = '#00FF00';      
            } else if (cataindex==3) { 
                realcolor = '#FF00FF';
                backcolor = '#0000FF';                      
            }  else if (cataindex==4) { 
                realcolor = '#FF0000';
                backcolor = '#0000FF';
            }

            const styleFunction = function (feature) {
        
                let name = feature.get('');
                let num = feature.get('');
                let nickname = feature.get('');
                let dia = feature.get('')/50;

                let label= ''
                let ch_offset=0
                if (nickname ==null) {
                    label = num +'\n'+name  
                    ch_offset = -25
                } else{
                    label = num +'\n'+name+'\n'+nickname
                    ch_offset = -30
                }
                let mystyle = new Style({      
                    // text: new Text({
                    // text: label,
                    // font: 'bold 11px Calibri,sans-serif',
                    // fill: new Fill({
                    //     color: 'black',
                    // }),
                    // stroke: new Stroke({
                    //     color: 'white',
                    //     width: 2,
                    // }),
                    // offsetY : ch_offset
                    // }),
                    // image: new CircleStyle({
                    //     radius: dia,
                    //     fill: new Fill({
                    //             color: 'red',
                    //     }),
                    // }),
                    stroke: new Stroke({
                        color: realcolor,
                        width: 2,
                    }),
                });

                return mystyle;  
            }
            const polyline_source = new VectorSource({
                url: fname,
                projection: 'EPSG:4326',
                format: new GeoJSON(),
            });
            const polylineLayer = new VectorLayer({
                source: polyline_source,
                style : styleFunction,
            });

            return polylineLayer;
        },
        
        make_cafe_data() {
            let features = jeju_cafe_all_data.features;
            var j_source = new VectorSource()
            var s_source = new VectorSource()

            features.forEach( element => {
                let name = element.place_name                
                let my_address = element.address_name
                let city = element.address_name.split(' ')[1]
                let my_link = element.place_url
                let position = new Point([element.x, element.y])
                let phone = element.phone                
                var feat = new Feature({
                    geometry: position,
                    name : name,
                    address: my_address,
                    link: my_link,
                    phone: phone,
                })                
                if ( city === '')  {
                    j_source.addFeature(feat)
                } else if ( city === '') {
                    s_source.addFeature(feat)
                }
            });            
            jejucafeLayer = new VectorLayer({
                source: j_source,
                style: new Style({
                    image:  new Icon({
                                src:'/img/jeju_cafe.png',
                                scale: 0.7,
                            })
                })
            })
            seogwipocafeLayer = new VectorLayer({
                source: s_source,
                style: new Style({
                    image:  new Icon({
                                src:'/img/seogwipo_cafe.png',
                                scale: 0.7,
                            })
                })
            })
        },

