<template>
    <Header :homeDisplay="this.homeDisplay" />
    <div class="main_display">        
        <b-button pill variant="primary" @click="changeMap">{{this.nextmap}} </b-button>            
        <div class="map_display">        
            <MyKakaoMap     v-if="this.mapindex==0"/> 
            <MyNaverMap     v-if="this.mapindex==1"/>
            <MyOpenLayerMap v-if="this.mapindex==2"/>
            <MyGoogleMap    v-if="this.mapindex==3"/>
        </div>
    </div>
</template>

<script>
import {ref} from 'vue';
import Header from "@/components/Header.vue";
const homeDisplay = ref(false);
import MyKakaoMap from "@/components/KakaoMap";
import MyNaverMap from "@/components/NaverMap";
import MyGoogleMap from "@/components/GoogleMap";
import MyOpenLayerMap from "@/components/OpenLayerMap"

// import oleum from "../assets/jeju_oleum.json"
// import olle from "../assets/jeju_olle_trail.json"
// import halla from "../assets/jeju_halla_trail.json"
// import hiking from "../assets/jeju_hiking_total.json"

// import { ref } from 'vue'
// import { useElementSize } from '@vueuse/core'

// var init_height = '380px';
// var init_width = '350px'

export default{
    name: "HomeView",

    components: {
        Header,
        MyKakaoMap,
        MyNaverMap,
        MyGoogleMap,
        MyOpenLayerMap,
    },

    data() {
        return{
            homeDisplay:homeDisplay,
            mapindex: 0,
            nextmap:'',
            // mapindex: 0,
            // nextmap:'',
            // height: init_height,
            // width: init_width,
            // oleum_data: oleum,
            // olle_data: olle,
            // halla_data: halla,
            // hiking_data: hiking,
        }
    },

    methods:{

        changeMap() {            
            
            this.mapindex++;           
            
            if (this.mapindex==0) { this.nextmap=''}
            if (this.mapindex==1) { this.nextmap=''}
            if (this.mapindex==2) { this.nextmap=''}
            if (this.mapindex==3) { this.nextmap=''}
            
            if (this.mapindex > 3) {
                this.mapindex=0;
                this.nextmap='';
            }
        },
    }

}
</script>

<style scoped>
.main_display {
    padding: 5px;
}
.map_display {
    padding-top : 3px;
}
</style>

