<template>
    <div id="map"></div>
    <div class="bot1">
        <b-button pill variant="primary" @click="displayOleum"></b-button>
        <b-button pill variant="primary" @click="displayOlle"></b-button>
        <b-button pill variant="primary" @click="displayHalla"> </b-button>
        <b-button pill variant="primary" @click="displayHiking"></b-button>
        <b-button pill variant="primary" @click="displayzpt"></b-button>
    </div>
    <div class="bot2">
        <b-button pill variant="success" @click="displayCafe(true)"></b-button>
        <b-button pill variant="success" @click="displayRest(true)"></b-button>
        <b-button pill variant="success" @click="displayHotel(true)"></b-button>
    </div>
</template>

<script>
import { toRaw } from "vue";
import { ref } from 'vue';
import { Loader } from "@googlemaps/js-api-loader"

import oleum_data from "../assets/jeju_oleum.json"
import olle_data from "../assets/jeju_olle_trail.json"
import halla_data from "../assets/jeju_halla_trail.json"
import hiking_data from "../assets/jeju_hiking_total.json"
import zpt_data from "../assets/jeju_zpt.json"

import jeju_rest_all_data from "../assets/jeju_rest_all.json"
import jeju_cafe_all_data from "../assets/jeju_cafe_all.json"
import hotel_all_data from "../assets/jeju_hotel_all.json"


var apikey= ref('');

var customOverlay=({});
var infoWindow=({});

var cataindex=0;
var hallaIndex=true;
var halla_polylines=[];
var olleIndex=true;
var olle_polylines=[];
var hikingIndex=true;
var hike_polylines=[];
var zptIndex=true;
var zpt_polylines=[];
var oleumIndex=true;
var oleum_circles=[];

var jejucafeIndex=true;
var jejucafeMarkers=[];
var seogwipocafeIndex=true;
var seogwipocafeMarkers=[];
var cafeIndex = true;

var jejurestIndex=true;
var jejurestMarkers=[];
var seogwiporestIndex=true;
var seogwiporestMarkers=[];
var restIndex = true;

var jejuhotelIndex=true;
var jejuhotelMarkers=[];
var seogwipohotelIndex=true;
var seogwipohotelMarkers=[];
var hotelIndex= true;

var changeMaptype;
var currentTypeId;
var loader;

var changeLayer;
var currentLayer;

export default {
    name: "GoogleMap",

    data() {
        return {
            // jejusi_index:jejusi_index,
            // seogwiposi_index:seogwiposi_index,
            // my_btns:[
            //     { caption: '', state:false, func : this.displayCafe(true)},
            //     { caption: '', state:true , func : this.displayRest(true)},
            //     { caption: '', state:false, func : this.displayHotel(true)},
            // ]                        
        };
    },

    created() {
        apikey = process.env.VUE_APP_GOOGLEMAP_API_KEY;        
        // console.log('api_key ', apikey);
        
    },

    mounted() {
        loader = new Loader({
            apiKey: apikey,
            version: "weekly",
            // ...additionalOptions,
        });

        this.initMap();
    },

    methods: {

        initMap() {
            
            const container = document.getElementById("map");            
            
            loader.load().then(async () => {

                const { Map } = await google.maps.importLibrary("maps");
                // const { AdvancedMarkerElement } = await google.maps.importLibrary("marker");
                const mapProp = {
                    center: { lat :33.38, lng: 126.58 },
                    zoom: 10,
                    mapTypeControl: true,
                    mapTypeControlOptions: {
                    style: google.maps.MapTypeControlStyle.HORIZONTAL_BAR,
                    position: google.maps.ControlPosition.TOP_RIGHT,
                    },
                }                

                this.map = new Map(container, mapProp);

                // const leftControlDiv = document.createElement("div")
                // const control = new LeftControl(leftControlDiv, toRaw(this.map))
                // leftControlDiv.index=1
                // leftControlDiv.style.paddingTop = "10px"
                // this.map.control[google.maps.ControlPosition.TOP_LEFT].push(leftControlDiv)

                var content = document.createElement('div');
                content.className = 'modes';

                if ( container.clientWidth > 340 ) {                
                    content.innerHTML =  
                    // '<div class="modes">\n'+
                    // '<button id="hybrid"></button>\n' +
                    // '<button id="terrain"></button>\n' +
                    '<button id="traffic"></button>\n' +
                    '<button id="bicycle"></button>\n' +
                    // '<button id="roadview"></button>\n' +
                    '<button id="jejusi"></button>\n' +
                    '<button id="seogwiposi"></button>\n' +
                    '</div>\n';
                } else {                
                    content.innerHTML =  
                    // '<div class="modes">\n'+
                    // '<button id="hybrid"></button>\n' +
                    // '<button id="terrain"></button>\n' +
                    '<button id="traffic"></button>\n' +
                    '<button id="bicycle"></button>\n' +
                    // '<div>\n'+
                    //     '<button id="bicycle"></button>\n' +
                    //     // '<button id="roadview"></button>\n' +
                    // '</div>\n'+
                    '<div>\n'+
                    '   <button id="jejusi"></button>\n' +
                    '   <button id="seogwiposi"></button>\n' +
                    '</div>\n'+
                    '</div>\n';
                }            

                container.appendChild(content);
                // console.log('google ', container)                

                // this.map.controls[google.maps.ControlPosition.TOP_LEFT].push(content);
            
                // const hybrid_btn = document.querySelector('#hybrid');  
                // hybrid_btn.addEventListener('click', (event) => {
                //     this.onSelectMap('hybrid')
                // });

                // const terrain_btn = document.querySelector('#terrain');  
                // terrain_btn.addEventListener('click', (event) => {
                //     this.onSelectMap('terrain')
                // });

                const bicycle_btn = document.querySelector('#bicycle');  
                bicycle_btn.addEventListener('click', (event) => {
                    this.onSelectMap('bicycle')
                });

                const traffic_btn = document.querySelector('#traffic');  
                traffic_btn.addEventListener('click', (event) => {
                    this.onSelectMap('traffic')
                });

                // const roadview_btn = document.querySelector('#roadview');  
                // roadview_btn.addEventListener('click', (event) => {
                //     this.onSelectMap('roadview')
                // });

                // const use_district_btn = document.querySelector('#use_district');  
                // use_district_btn.addEventListener('click', (event) => {
                //     this.onSelectMap('use_district')
                // });

                const jejusi_btn = document.querySelector('#jejusi');  
                jejusi_btn.addEventListener('click', (event) => {
                    if ( !cafeIndex ) {
                        jejucafeIndex = jejucafeIndex ? false : true
                        seogwipocafeIndex = false
                        cafeIndex = false                                
                        this.displayCafe(false)
                    }

                    if ( !restIndex ) {
                        jejurestIndex = jejurestIndex ? false : true
                        seogwiporestIndex = false
                        restIndex = false                                
                        this.displayRest(false)
                    }

                    if ( !hotelIndex ) {
                        jejuhotelIndex = jejuhotelIndex ? false : true
                        console.log('jejuhotelIndex', jejuhotelIndex)
                        seogwipohotelIndex = false
                        hotelIndex = false                                
                        this.displayHotel(false)
                    }               
                    
                });

                const seogwiposi_btn = document.querySelector('#seogwiposi');  
                seogwiposi_btn.addEventListener('click', (event) => {
                    
                    if ( !cafeIndex ) {
                        seogwipocafeIndex = seogwipocafeIndex ? false : true
                        cafeIndex = false
                        jejucafeIndex = false
                        this.displayCafe(false)
                    } 
                    if ( !restIndex ) {
                        seogwiporestIndex = seogwiporestIndex ? false : true
                        restIndex = false
                        jejurestIndex = false
                        this.displayRest(false)
                    } 
                    if ( !hotelIndex ) {
                        seogwipohotelIndex = seogwipohotelIndex ? false : true
                        hotelIndex = false
                        jejuhotelIndex = false
                        this.displayHotel(false)
                    }               
                    
                });

            });
            // console.log(div);
            // // toRaw(this.map).relayout();            
            // this.make_cafe_data()
            // this.make_rest_data()
            // this.make_hotel_data()
        },
        
        make_cafe_data() {
            cataindex=10;
            let features = jeju_cafe_all_data.features;
            features.forEach( element => {
                let name = element.place_name;                
                let my_address = element.address_name;
                let city = element.address_name.split(' ')[1];
                let my_link = element.place_url;
                let position = new google.maps.LatLng(element.y, element.x)
                let phone = element.phone;                        
                this.citydrawingMarker(cataindex, toRaw(this.map), position, name, my_address, my_link, phone, city);                
            });

        },

        make_rest_data() {
            cataindex=11;
            let features = jeju_rest_all_data.features;
            features.forEach( element => {   
                let name = element.place_name;                
                let my_address = element.address_name;
                let city = element.address_name.split(' ')[1];
                let my_link = element.place_url;
                let position = new google.maps.LatLng(element.y, element.x)
                let phone = element.phone;
                this.citydrawingMarker(cataindex, toRaw(this.map), position, name, my_address, my_link, phone, city);                
            });

        },

        make_hotel_data() {
            cataindex=12;
            let features = hotel_all_data.features;            
            features.forEach( element => {   
                let name = element.place_name;                
                let my_address = element.address_name;
                let city = element.address_name.split(' ')[1];
                let my_link = element.place_url;
                let position = new google.maps.LatLng(element.y, element.x)
                let phone = element.phone;
                this.citydrawingMarker(cataindex, toRaw(this.map), position, name, my_address, my_link, phone, city);
            });

        },

        citydrawingMarker(cataindex, map, position, name, my_address, my_link, phone, city) {

            var imageSrc = '';
            
            if (cataindex == 10) {
                if ( city === '') {
                    imageSrc = '/img/jeju_cafe.png';

                } else if ( city === '') {
                    imageSrc = '/img/seogwipo_cafe.png';
                }                
            } else if (cataindex == 11) {
                if ( city === '') {
                    imageSrc = '/img/jeju_rest.png';

                } else if ( city === '') {
                    imageSrc = '/img/seogwipo_rest.png';
                }                
            } else if (cataindex ==12) {
                if ( city === '') {
                    imageSrc = '/img/jeju_hotel.png';

                } else if ( city === '') {
                    imageSrc = '/img/seogwipo_hotel.png';
                }
            }

            // var imageSize = new google.maps.Size(16, 17);
            // var imageOption = {offset: new google.maps.Point(8, 9)};            
            // var markerImage = new google.maps.MarkerImage(imageSrc, imageSize, imageOption)
            // var imageSize = new google.maps.Size(16, 17);
            // var imageOption = {offset: new google.maps.Point(8, 9)};            
            // var markerImage = new google.maps.MarkerImage(imageSrc, imageSize, imageOption)

            var marker = new google.maps.Marker({
                position : position,
                // label: name,
                icon : imageSrc,
                scale : 0.05,
            });


            // var content =  '<div style="width:250px; height: 150px; padding: 5px;">';
            //     content += '    <a href='+my_link+' target="_blank"><strong> [  : '+name+']</strong></a>';
            //     content += '    <div>  : '+my_address+' </div>';
            //     content += '</div>';
            
            // var content = '<div style="width:250px; height: 100px; padding: 5px;"> <div></div>' +
            var content = '<div class="placeinfo">'+
                '   <a class="title" href="' + my_link + '" target="_blank" title="' + name + '">' + name + '</a>';
                content += '<div> <span title="' + my_address + '">' + my_address + '</span> </div>';   
                content += '      <span class="tel"> Tel. ' + phone + '</span>'; 
                content += '<div class="after"></div>';

            var infowindow = new google.maps.InfoWindow({
                content: content,
            });

            new google.maps.event.addListener(marker, 'click', function() {                                  
                infowindow.open(map, marker);
            })

            if (cataindex ==10) {
                if (city === '') jejucafeMarkers.push(marker)
                else if (city === '') seogwipocafeMarkers.push(marker)
            } else if (cataindex ==11) {
                if (city === '') jejurestMarkers.push(marker)
                else if (city === '') seogwiporestMarkers.push(marker)
            } else if (cataindex ==12) {
                if (city === '') jejuhotelMarkers.push(marker)
                else if (city === '') seogwipohotelMarkers.push(marker)
            }            
        },

        displayCafe(main_index){
            // console.log(' cafeIndex ', cafeIndex, 'jejucafelIndex ',jejucafeIndex, 'seogwipocafeIndex ', seogwipocafeIndex  )
            if(cafeIndex){
                this.make_cafe_data()                
                if ( jejucafeIndex ) jejucafeMarkers.map((cur) => cur.setMap(toRaw(this.map)))
                if ( seogwipocafeIndex ) seogwipocafeMarkers.map((cur) => cur.setMap(toRaw(this.map)))
                cafeIndex = false
                jejucafeIndex = false
                seogwipocafeIndex = false
            } else {
                if (main_index) {
                    jejucafeMarkers.map((cur) => cur.setMap(null))
                    seogwipocafeMarkers.map((cur) => cur.setMap(null))
                    cafeIndex = true
                    jejucafeIndex = true
                    seogwipocafeIndex = true
                } else {
                    if ( jejucafeIndex ) jejucafeMarkers.map((cur) => cur.setMap(toRaw(this.map)))
                    else jejucafeMarkers.map((cur) => cur.setMap(null))                    
                    if ( seogwipocafeIndex ) seogwipocafeMarkers.map((cur) => cur.setMap(toRaw(this.map)))
                    else seogwipocafeMarkers.map((cur) => cur.setMap(null))
                }               
            }
        },

        displayRest(main_index){
            // console.log(' restIndex ', restIndex, 'jejurestIndex ',jejurestIndex, 'seogwiporestIndex ', seogwiporestIndex  )
            if(restIndex){
                this.make_rest_data()                
                if ( jejurestIndex ) jejurestMarkers.map((cur) => cur.setMap(toRaw(this.map)))
                if ( seogwiporestIndex ) seogwiporestMarkers.map((cur) => cur.setMap(toRaw(this.map)))
                restIndex = false
                jejurestIndex = false
                seogwiporestIndex = false
            } else {
                if (main_index) {
                    jejurestMarkers.map((cur) => cur.setMap(null))
                    seogwiporestMarkers.map((cur) => cur.setMap(null))
                    restIndex = true
                    jejurestIndex = true
                    seogwiporestIndex = true
                } else {
                    if ( jejurestIndex ) jejurestMarkers.map((cur) => cur.setMap(toRaw(this.map)))
                    else jejurestMarkers.map((cur) => cur.setMap(null))                    
                    if ( seogwiporestIndex ) seogwiporestMarkers.map((cur) => cur.setMap(toRaw(this.map)))
                    else seogwiporestMarkers.map((cur) => cur.setMap(null))
                }               
            }
        },

        displayHotel(main_index){
            // console.log(' hotelIndex ', hotelIndex, 'jejuhotelIndex ',jejuhotelIndex, 'seogwipohotelIndex ', seogwipohotelIndex  )
            if(hotelIndex){
                this.make_hotel_data()                
                if ( jejuhotelIndex ) jejuhotelMarkers.map((cur) => cur.setMap(toRaw(this.map)))
                if ( seogwipohotelIndex ) seogwipohotelMarkers.map((cur) => cur.setMap(toRaw(this.map)))
                hotelIndex = false
                jejuhotelIndex = false
                seogwipohotelIndex = false
            } else {
                if (main_index) {
                    jejuhotelMarkers.map((cur) => cur.setMap(null))
                    seogwipohotelMarkers.map((cur) => cur.setMap(null))
                    hotelIndex = true
                    jejuhotelIndex = true
                    seogwipohotelIndex = true
                } else {
                    if ( jejuhotelIndex ) jejuhotelMarkers.map((cur) => cur.setMap(toRaw(this.map)))
                    else jejuhotelMarkers.map((cur) => cur.setMap(null))                    
                    if ( seogwipohotelIndex ) seogwipohotelMarkers.map((cur) => cur.setMap(toRaw(this.map)))
                    else seogwipohotelMarkers.map((cur) => cur.setMap(null))
                }               
            }
        },        

        onSelectMap: function(maptype) {
            
            if (maptype === 'traffic') {
                if ( currentLayer != null ) currentLayer.setMap(null)
                changeLayer = new google.maps.TrafficLayer()
                // console.log('ddd ', changeLayer)        
                changeLayer.setMap(toRaw(this.map))
                // currentLayer = changeLayer                
            } 
            // else if (maptype === 'roadview') {
            //     if ( currentLayer !== null ) currentLayer.setMap(null)
            //     changeLayer = new google.maps.TransitLayer()
            //     changeLayer.setMap(toRaw(this.map))
            //     currentLayer = changeLayer
            // } 
            else if (maptype === 'bicycle') {
                if ( currentLayer != null ) currentLayer.setMap(null)
                changeLayer = new google.maps.BicyclingLayer()
                changeLayer.setMap(toRaw(this.map))
                currentLayer = changeLayer
            } 
            //
            
            // else if (maptype === 'terrain') {                    
            //     if ( currentLayer != null ) currentLayer.setMap(null)
            //     if ( this.map.getMapTypeId() === naver.maps.MapTypeId.TERRAIN) {
            //         changeMaptype = naver.maps.MapTypeId.NORMAL;
            //         this.map.setMapTypeId(changeMaptype);
            //     } else {
            //         changeMaptype = naver.maps.MapTypeId.TERRAIN;
            //         this.map.setMapTypeId(changeMaptype);
            //     }                    
                
            // } 

            // else if (maptype === 'use_district') {
            //     if ( currentLayer != null ) currentLayer.setMap(null)
            //     changeLayer = new naver.maps.CadastralLayer();
            //     changeLayer.setMap(toRaw(this.map))
            //     currentLayer = changeLayer
            // }
            
            currentTypeId = changeMaptype;          
        },

        displayOleum() {

            cataindex=1;
            if(oleumIndex) {

                if(oleum_circles.length <=0 ) {
                    let features = oleum_data.features;
                    features.forEach( element => {
                        let position = new google.maps.LatLng(element.geometry.coordinates[1], element.geometry.coordinates[0]);
                        let dia = element.properties['']; 
                        let name = element.properties['']
                        this.drawingCircle(cataindex, toRaw(this.map), position, dia, name);                        
                    });                    
                } else {
                    oleum_circles.map( (cur) => cur.setMap(toRaw(this.map)));
                }
                oleumIndex = false;
            } else {
                oleum_circles.map( (cur) => cur.setMap(null));
                oleumIndex = true;
            }
            
        },

        displayOlle() {

            cataindex=1;
            if(olleIndex) {
                if( olle_polylines.length <=0 ) {
                    let features = olle_data.features;            
                    features.forEach( element => {      
                        let name = element.properties.name;             
                        let coord = element.geometry.coordinates;
                        coord.forEach( points => {
                            var path=[];
                            points.forEach ( point => {
                                let position = new google.maps.LatLng(point[1], point[0]);
                                path.push(position);
                            });
                            this.drawingPolyline(cataindex,toRaw(this.map), path, name);
                        });
                    });
                } else {
                    olle_polylines.map( (cur) => cur.setMap(toRaw(this.map)));
                }                
                olleIndex = false;
            } else {
                olle_polylines.map( (cur) => cur.setMap(null));
                olleIndex = true;
            }            
        },

        displayHalla() {

            cataindex=2; 
            if(hallaIndex) {
                if ( halla_polylines.length <=0 ) {
                    let features = halla_data.features;            
                    features.forEach( element => { 
                        let name = element.properties.name;  
                        let coord = element.geometry.coordinates;
                        coord.forEach( points => {
                            var path=[];
                            points.forEach ( point => {
                                let position = new google.maps.LatLng(point[1], point[0]);
                                path.push(position);
                            });
                            this.drawingPolyline(cataindex, toRaw(this.map), path, name);                     
                        });
                    });
                } else {
                    halla_polylines.map( (cur) => cur.setMap(toRaw(this.map)));
                }
                hallaIndex = false;
            } else {
                halla_polylines.map( (cur) => cur.setMap(null));
                hallaIndex = true;
            }
            
        },

        displayHiking() {

            cataindex=3;
            if(hikingIndex){
                if ( hike_polylines.length <=0 ) {                    
                    let features = hiking_data.features;
                    features.forEach( element => {                        
                        let name = ' '+ element.properties.PMNTN_NM;                
                        let coord = element.geometry.coordinates;
                        coord.forEach( points => {
                            var path=[];
                            points.forEach ( point => {
                                let position = new google.maps.LatLng(point[1], point[0]);
                                path.push(position);
                            });
                            this.drawingPolyline(cataindex, toRaw(this.map), path, name);            
                        });
                    });
                } else {
                    hike_polylines.map( (cur) => cur.setMap(toRaw(this.map)));                    
                }
                hikingIndex = false;
            } else {
                hike_polylines.map( (cur) => cur.setMap(null));
                hikingIndex = true;
            }          
        },

        displayzpt(){

            cataindex=4;
            if(zptIndex){
                if ( zpt_polylines.length <=0 ) {                    
                    let features = zpt_data.features;
                    features.forEach( element => {                        
                        let name = element.properties.name;                
                        let coord = element.geometry.coordinates;

                        if ( element.geometry.type=='LineString' ) {                            
                            
                            var path=[];                            
                            coord.forEach( point => {
                                let position = new google.maps.LatLng(point[1], point[0]);
                                path.push(position);              
                            });
                            this.drawingPolyline(cataindex, toRaw(this.map), path, name);

                        } else if (element.geometry.type=='MultiLineString'){
                            
                            coord.forEach( points => {
                                var path=[];
                                points.forEach ( point => {
                                    let position = new google.maps.LatLng(point[1], point[0]);
                                    path.push(position);
                                });
                                this.drawingPolyline(cataindex, toRaw(this.map), path, name);            
                            });

                        }
                    });
                } else {
                    zpt_polylines.map( (cur) => cur.setMap(toRaw(this.map)));                    
                }
                zptIndex = false;
            } else {
                zpt_polylines.map( (cur) => cur.setMap(null));
                zptIndex = true;
            }

        },

        drawingCircle(cataindex, map, position, dia, name) {
            
            let fillrealcolor='';
            let backcolor='';
            
            if (cataindex==1) {
                fillrealcolor = '#E51D1A';
                backcolor = '#FF0000';      
            } else if (cataindex==2) {
                fillrealcolor = '#00FF0F';
                backcolor = '#FF0000';      
            } else if (cataindex==3) { 
                fillrealcolor = '#FF00FF';
                backcolor = '#0000FF';                      
            }  else if (cataindex==4) { 
                fillrealcolor = '#FF0000';
                backcolor = '#0000FF';
            }

            var circle = new google.maps.Circle({
                    map: map,
                    center : position,
                    radius: dia,    
                    strokeColor: '#5347AA',
                    strokeOpacity: 0.5,
                    strokeWeight: 2,
                    fillColor: fillrealcolor,
                    fillOpacity: 0.3,
                    //    
                    // radius: 5000, //     
                    // strokeWeight: 5, //   
                    // strokeColor: '#75B8FA', //  
                    // strokeOpacity: 1, //    1 0   0  
                    // strokeStyle: 'dashed', //   
                    // fillColor: '#CFE7FF', //  
                    // fillOpacity: 0.7  //      
            });           
            
            var infoBox = new google.maps.InfoWindow({
                content: name + '  [ '+dia+' m]',
                // yAnchor: 2.5,
            });
            
            // var infoBox = new google.maps.OverlayView({
            //     content: name + '  [ '+dia+' m]',
            //     yAnchor: 1.5,
            // });

            new google.maps.event.addListener(circle, 'mouseover', function(mouseEvent) {                                  
                circle.setOptions({
                    fillOpacity: 0.6,
                    strokeStyle: 'dashed'
                });
                infoBox.setPosition(mouseEvent.latLng);
                infoBox.setMap(map);
            })
            
            new google.maps.event.addListener(circle, 'mousemove', function(mouseEvent) {                
                infoBox.setPosition(mouseEvent.latLng);
                infoBox.setMap(map);
            });
            
            new google.maps.event.addListener(circle, 'mouseout', function() {                
                circle.setOptions({
                    fillOpacity: 0.3,
                    strokeStyle: 'line'
                });
                infoBox.setMap(null);   
            });

            if (cataindex==1) {
                oleum_circles.push(circle);
            } 
            // else if (cataindex==2) {
            //     halla_polylines.push(polyline);
            // } else if (cataindex==3) { 
            //     hike_polylines.push(polyline);
            // } else if (cataindex==4) { 
            //     zpt_polylines.push(polyline);
            // }  
        },

        drawingPolyline(cataindex, map, path, name) {

            let realcolor='';
            let backcolor='';

            if (cataindex==1) {
                realcolor = '#0004FF';
                backcolor = '#FF0000';      
            } else if (cataindex==2) {
                realcolor = '#FF0000';
                backcolor = '#00FF00';      
            } else if (cataindex==3) { 
                realcolor = '#FF00FF';
                backcolor = '#0000FF';                      
            }  else if (cataindex==4) { 
                realcolor = '#FF0000';
                backcolor = '#0000FF';
            }

            var polyline = new google.maps.Polyline({
                map: map,
                path: path, //    
                strokeWeight: 2, //   
                strokeColor: realcolor, //  
                strokeOpacity: 0.5, //    1 0   0  
                strokeStyle: 'solid'                 
            });            
            
            var infoBox = new google.maps.InfoWindow({
                content: name,
                yAnchor: 1,
            });

            new google.maps.event.addListener(polyline, 'mouseover', function(mouseEvent) {                                  
                polyline.setOptions({
                    strokeWeight: 7,
                    strokeColor: backcolor,
                    strokeOpacity: 1,
                    strokeStyle: 'solid'
                });
                infoBox.setPosition(mouseEvent.latLng);
                infoBox.setMap(map);
                // console.log('ddd ', infoBox);
            })
            
            new google.maps.event.addListener(polyline, 'mousemove', function(mouseEvent) {                
                // customOverlay.setPosition(mouseEvent.latLng); 
                infoBox.setPosition(mouseEvent.latLng);
                infoBox.setMap(map);
            });
            
            new google.maps.event.addListener(polyline, 'mouseout', function() {                
                polyline.setOptions({
                    strokeWeight: 2, //   
                    strokeColor: realcolor, //  
                    strokeOpacity: 0.5, //    1 0   0  
                    strokeStyle: 'solid' 
                })
                infoBox.setMap(null);   
            });

            if (cataindex==1) {
                olle_polylines.push(polyline);
            } else if (cataindex==2) {
                halla_polylines.push(polyline);
            } else if (cataindex==3) { 
                hike_polylines.push(polyline);
            } else if (cataindex==4) { 
                zpt_polylines.push(polyline);
            }  
        },
        
    },

};
</script>

